body{
    background-color: #f8f9fa !important;
}

.jumbotron {
    padding: 4rem 2rem;
    margin-bottom: 2rem;
    background-color: #e7edf3 !important;
    border-radius: .3rem;  
}

footer{
    width: 100vw;
    top: 85vh;
    height: 15vh;
    position: absolute;
    background-color: white;
    padding: 20px;
}

.with_projects{
    display: flex;
    width: 100%;
}

.with_projects_left{
    width: 50%;
}
.with_projects_right{
    width: 50%;
    padding: 15px;
}

.with_projects_right #projects_list{
    width: 100%;
}

.import_project_separator{
    text-align: center;
}

.adno-about{
    height: 78vh;
    overflow: scroll;
    padding: 35px;
}

.hide-footer{
    display: none;
}

@media (max-width: 1500px) {
    footer {
        width: 100vw;
        height: 20vh;
        background-color: #fff;
        padding: 20px;
        position: absolute;
        top: 80vh;
    }

    .jumbotron{
        border-radius: 0.3rem !important;
        margin-bottom: 2rem !important;
        padding: 2rem !important;
        background-color: #e7edf3 !important;
    }
}